body {
  width: 100%;
  min-width: 320px;
  font-family: "Geometria Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 17px;
  line-height: 22px;
  margin: 0;
  padding: 0;
  color: #000;
  background: #fff;
}
@media (min-width: 768px) {
  body {
    background: linear-gradient(90deg, #f1f1f1 50%, #fff 50%);
  }
}
.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 200;
}
.header__wrapper {
  display: flex;
  position: relative;
  width: 100%;
  max-width: 1440px;
  justify-content: space-between;
  align-items: center;
  padding: 15.8px 20px;
  margin: 0 auto;
}
.header__logo-image {
  display: block;
}
.header__navigation-toggle {
  position: relative;
  width: 20px;
  height: 14px;
  background: none;
  border: none;
  cursor: pointer;
}
.header__navigation-toggle::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  top: 0;
  left: 0;
  background: #000;
  box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .header {
    position: absolute;
  }
  .header__wrapper {
    align-items: flex-start;
    padding: 34.8px 80px 30px 34.4px;
  }
  .header__logo svg {
    position: fixed;
  }
  .header__navigation-toggle {
    margin: 85px 17px 0 0;
  }
  .header__navigation-toggle::after {
    position: absolute;
    content: "";
    width: 54px;
    height: 54px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url("../img/coronal.svg") no-repeat;
    background-size: cover;
  }
}
.main {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  max-width: 1440px;
  height: 100vh;
  margin: 0 auto;
  background: #F7E611;
}
@media (min-width: 768px) {
  .main {
    justify-content: center;
    padding: 0 80px;
    background: none;
  }
}
@media (min-width: 1200px) {
  .main {
    height: auto;
    justify-content: flex-start;
  }
}
.slider {
  position: relative;
  width: 100%;
  padding: 83px 0 0;
}
.slider img {
  display: block;
}
.slider__title {
  display: none;
}
.slider__back {
  position: relative;
  width: 236px;
  height: 184.11px;
  z-index: 10;
}
.slider__back picture {
  position: relative;
  background: #F7E611;
  animation: slide 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.slider__back img {
  width: 100%;
  height: 184.11px;
  object-fit: cover;
  object-position: right;
  opacity: 0.2;
}
.slider__front {
  position: relative;
  width: 100%;
  height: 318px;
  margin-top: -17.11px;
  z-index: 20;
}
.slider__front picture {
  position: relative;
  background: #F7E611;
  animation: scale 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.slider__front img {
  width: 100%;
  height: 318px;
  object-fit: cover;
  opacity: 0.4;
}
.slider__control {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  border: none;
  border-left: 4px solid #000;
  border-bottom: 4px solid #000;
  background: none;
  opacity: 0.4;
  z-index: 200;
  cursor: pointer;
}
.slider__control:focus {
  outline: none;
  opacity: 1;
}
.slider__control--prev {
  left: 8%;
  transform: translateY(-50%) rotate(45deg);
}
.slider__control--next {
  right: 8%;
  transform: translateY(-50%) rotate(225deg);
}
@media (min-width: 768px) {
  .slider {
    padding: 80px 0 0;
  }
  .slider__title {
    display: block;
    position: absolute;
    width: 495px;
    height: 70px;
    top: 50%;
    left: calc(50% - 247.5px);
    margin: 0;
    font-family: "Philosopher Regular", "Arial", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 70px;
    line-height: 70px;
    text-transform: uppercase;
    letter-spacing: 25px;
    color: #fff;
    z-index: 110;
  }
  .slider__back {
    width: 40%;
  }
  .slider__back img {
    width: 100%;
  }
  .slider__front {
    width: 90%;
    margin: -17.11px auto 0;
  }
  .slider__front img {
    width: 100%;
    opacity: 0.2;
  }
}
@media (min-width: 1200px) {
  .slider {
    padding: 97px 0 126px;
  }
  .slider__title {
    width: 995px;
    height: 152px;
    top: 325px;
    left: calc(50% - 497.5px);
    font-size: 140px;
    line-height: 152px;
    letter-spacing: 50.5px;
  }
  .slider__back {
    width: 800px;
    height: 525px;
    margin: 0 auto 0 0;
  }
  .slider__back picture {
    width: 800px;
    height: 525px;
  }
  .slider__back img {
    width: 100%;
    height: 100%;
  }
  .slider__front {
    width: 860px;
    height: 490px;
    margin: -438px 0 0 auto;
  }
  .slider__front picture {
    width: 860px;
    height: 490px;
  }
  .slider__front img {
    width: 100%;
    height: 100%;
  }
  .slider__control {
    top: 317px;
  }
  .slider__control--prev {
    left: 490px;
  }
  .slider__control--next {
    right: 165px;
  }
}
